import React from 'react';
import PanoramaFishEye from 'material-ui-icons/PanoramaFishEye';
import Done from 'material-ui-icons/Done';
import Clear from 'material-ui-icons/Clear';
import Typography from 'material-ui/Typography';

export default () => (
  <div
    style={{
      display: 'flex',
      flexDirection: 'row',
      alignItems: 'flex-end',
      justifyContent: 'space-between',
      borderBottom: '1px solid black'
    }}
  >
    <div>
      <Typography type="headline">Daily Maintenance checklist</Typography>
      <div
        style={{
          display: 'flex',
          flexDirection: 'row'
        }}
      >
        <Typography style={{ width: 150 }}>Machine Description :</Typography>
        <input className="bottom-border" placeholder="Enter Text" type="text" />
      </div>
      <div
        style={{
          display: 'flex',
          flexDirection: 'row'
        }}
      >
        <Typography style={{ width: 150 }}>Machine/Tooling No. :</Typography>
        <input className="bottom-border" placeholder="Enter Text" type="text" />
      </div>
      <div
        style={{
          display: 'flex',
          flexDirection: 'row'
        }}
      >
        <Typography style={{ width: 150 }}>Location :</Typography>
        <input
          className="bottom-border"
          placeholder="Enter Text"
          type="text"
          style={{ borderBottom: 'none' }}
        />
      </div>
    </div>
    <div
      style={{
        display: 'flex',
        justifyContent: 'flex-start',
        flexDirection: 'row',
        alignItems: 'flex-end'
      }}
    >
      <Typography style={{ paddingRight: 20 }}>Legend: </Typography>
      <Done />
      <Typography style={{ paddingRight: 20 }}> - Job complete, </Typography>
      <Clear />
      <Typography style={{ paddingRight: 20 }}>
        - m/c not operating,{' '}
      </Typography>
      <PanoramaFishEye />
      <Typography style={{ paddingRight: 20 }}>
        - public holiday/shutdown
      </Typography>
    </div>
    <div style={{ display: 'flex', flexDirection: 'column' }}>
      <Typography>Month:</Typography>
      <Typography>ZONE MAINTENANCE</Typography>
    </div>
  </div>
);



