import React, { Component } from 'react';
import './App.css';
import HeaderBar from './HeaderBar';
import DescriptionBar from './DescriptionBar';
import DataBar from './DataBar';

class App extends Component {
  render() {
    return (
      <div
        style={{
          display: 'flex',
          flexDirection: 'column'
        }}
      >
        <HeaderBar />
        <DescriptionBar />
        <DataBar />
      </div>
    );
  }
}

export default App;



