import React from 'react';
import Typography from 'material-ui/Typography';
import range from 'lodash/range';
import Button from 'material-ui/Button';
import TextField from 'material-ui/TextField';

export default class DataBar extends React.Component {
  state = {
    rows: []
  };
  addEntry = () => {
    const entry = range(31).reduce((reduced, item) => {
      return { ...reduced, [item]: '' };
    }, {});
    this.setState((prevState, props) => {
      return {
        rows: [...prevState.rows, entry]
      };
    });
  };
  render() {
    console.log(this.state.rows);
    return (
      <div>
        <div style={{ display: 'flex', flexDirection: 'column' }}>
          <div
            style={{
              display: 'flex',
              flexDirection: 'row',
              justifyContent: 'space-between',
              alignItems: 'center',
              borderBottom: '1px solid'
            }}
          >
            <div
              style={{
                flex: 1,
                marginRight: 20,
                textAlign: 'center'
              }}
            >
              <Typography>Sr. No.</Typography>
            </div>
            <div
              style={{
                flex: 3,
                marginRight: 20,
                textAlign: 'center'
              }}
            >
              <Typography>DESCRIPTION / PARTS</Typography>
            </div>
            <div
              style={{
                flex: 3,
                marginRight: 20,
                textAlign: 'center'
              }}
            >
              <Typography>METHOD</Typography>
            </div>
            <div
              style={{
                flexDirection: 'column',
                flex: 7,
                display: 'flex',
                textAlign: 'center'
              }}
            >
              <Typography style={{ borderBottom: '1px solid' }}>
                Date
              </Typography>
              <div style={{ flexDirection: 'row', flex: 7, display: 'flex' }}>
                {range(31).map(item => (
                  <Typography
                    style={{
                      padding: 5,
                      width: 15,
                      borderLeft: '1px solid'
                    }}
                    key={item}
                  >
                    {item}
                  </Typography>
                ))}
              </div>
            </div>
          </div>
          {this.state.rows.map((entry, i) => (
            <div
              key={i}
              style={{
                display: 'flex',
                flexDirection: 'row',
                justifyContent: 'space-between',
                alignItems: 'center'
              }}
            >
              <div
                style={{
                  flex: 1,
                  marginRight: 20,
                  textAlign: 'center'
                }}
              >
                <Typography>{i + 1}</Typography>
              </div>
              <div
                style={{
                  flex: 3,
                  marginRight: 20,
                  textAlign: 'center'
                }}
              >
                <TextField />
              </div>
              <div
                style={{
                  flex: 3,
                  marginRight: 20,
                  textAlign: 'center'
                }}
              >
                <TextField />
              </div>
              <div
                style={{
                  flexDirection: 'column',
                  flex: 7,
                  display: 'flex',
                  textAlign: 'center'
                }}
              >
                <div style={{ flexDirection: 'row', flex: 7, display: 'flex' }}>
                  {range(31).map(item => (
                    <input
                      key={item}
                      style={{
                        border: 'none',
                        padding: 5,
                        width: 15,
                        borderLeft: '1px solid',
                        borderBottom: '1px solid'
                      }}
                      type="text"
                    />
                  ))}
                </div>
              </div>
            </div>
          ))}
        </div>
        <br />
        <Button raised onClick={this.addEntry} color="primary">
          Add Entry
        </Button>
      </div>
    );
  }
}



